"use client"

import { useState, useEffect } from "react"
import Image from "next/image"
import Link from "next/link"
import { Navbar } from "@/components/navbar"
import { Footer } from "@/components/footer"
import { ProductCard } from "@/components/product-card"
import { Carousel, CarouselContent, CarouselItem, CarouselNext, CarouselPrevious } from "@/components/ui/carousel"
import { supabase } from "@/lib/supabase" // Import supabase for fetching products
import { Spinner } from "@/components/ui/spinner"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { ExclamationTriangleIcon } from "@radix-ui/react-icons" // For error icons

// Define the product interface based on Supabase schema
interface HomePageProduct {
  id: string;
  name: string;
  price: number;
  description: string;
  image_urls: string[];
  category: string;
  stock: number;
  featured: boolean;
  created_at?: string;
}

export default function Home() {
  const heroImages = [
    { src: "/floreria-tienda-interior-elegante.jpg", alt: "Florer√≠a elegante" },
    { src: "/ramo-rosas-rojas-elegante.jpg", alt: "Ramo de rosas rojas" },
    { src: "/girasoles-flores-tropicales.jpg", alt: "Girasoles tropicales" },
    { src: "/lirios-blancos-elegantes.jpg", alt: "Lirios blancos elegantes" },
    { src: "/Gemini_Generated_Image_nq9787nq9787nq97.png", alt: "Arreglo floral colorido" },
  ];

  const [products, setProducts] = useState<HomePageProduct[]>([]);
  const [isLoadingProducts, setIsLoadingProducts] = useState(true);
  const [errorProducts, setErrorProducts] = useState<string | null>(null);

  useEffect(() => {
    const fetchProducts = async () => {
      setIsLoadingProducts(true);
      setErrorProducts(null);
      const { data, error } = await supabase
        .from("products")
        .select("*")
        .eq("featured", true) // Fetch only featured products for homepage, or adjust as needed
        .order("created_at", { ascending: false })
        .limit(4); // Limit to 4 for featured section

      if (error) {
        setErrorProducts(error.message);
        setProducts([]);
      } else {
        setProducts(data as HomePageProduct[]);
      }
      setIsLoadingProducts(false);
    };

    fetchProducts();
  }, []);

  return (
    <div style={{ backgroundColor: "#faf9f7" }} className="min-h-screen">
      <Navbar />

      {/* Hero Section with Carousel */}
      <section className="relative w-full h-[600px] md:h-[700px] overflow-hidden"> {/* Increased height for better visibility */}
        <Carousel className="w-full h-full">
          <CarouselContent className="h-full">
            {heroImages.map((image, index) => (
              <CarouselItem key={index} className="relative h-full">
                {index === 0 ? (
                  <img
                    src={image.src}
                    alt={image.alt}
                    style={{ objectFit: "cover", width: "100%", height: "100%", position: "absolute" }}
                  />
                ) : (
                  <Image
                    src={image.src}
                    alt={image.alt}
                    fill
                    className="object-cover"
                    priority={index === 0}
                  />
                )}
                <div className="absolute inset-0 bg-black bg-opacity-30 flex flex-col items-center justify-center text-white p-8 text-center">
                  <h1 className="text-6xl md:text-7xl font-serif font-bold mb-4 drop-shadow-lg">
                    Florer√≠a Encanto Floral
                  </h1>
                  <p className="text-xl md:text-2xl drop-shadow-md">
                    Tu destino para las flores m√°s frescas y hermosas
                  </p>
                </div>
              </CarouselItem>
            ))}
          </CarouselContent>
          {/* Ensure navigation buttons are clearly visible and interactive */}
          <CarouselPrevious className="-left-4" /> {/* Adjusted to outside carousel area */}
          <CarouselNext className="-right-4" /> {/* Adjusted to outside carousel area */}
        </Carousel>
      </section>

      {/* Products Section */}
      <section className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-20">
        <div className="text-center mb-12">
          <h2 style={{ color: "#1c1a38" }} className="text-4xl md:text-5xl font-serif font-bold mb-4">
            Nuestros Productos Destacados
          </h2>
          <p className="text-lg" style={{ color: "#5c5c5c" }}>
            Descubre nuestros arreglos florales m√°s hermosos y exclusivos
          </p>
        </div>

        {isLoadingProducts ? (
          <div className="flex justify-center items-center h-48">
            <Spinner className="size-8" />
          </div>
        ) : errorProducts ? (
          <div className="flex justify-center items-center h-48">
            <Alert variant="destructive">
              <ExclamationTriangleIcon className="h-4 w-4" />
              <AlertTitle>Error al cargar productos</AlertTitle>
              <AlertDescription>{errorProducts}</AlertDescription>
            </Alert>
          </div>
        ) : products.length === 0 ? (
          <div className="text-center text-gray-500 text-lg py-12">
            No hay productos destacados disponibles.
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
            {products.map((product) => (
              <ProductCard
                key={product.id}
                id={product.id}
                name={product.name}
                price={product.price}
                image={product.image_urls[0] || "/placeholder.svg"}
                description={product.description}
                category={product.category}
              />
            ))}
          </div>
        )}

        <div className="text-center">
          <Link href="/products" className="btn-primary inline-block hover:scale-105 transition-transform">
            Ver Todos los Productos
          </Link>
        </div>
      </section>

      {/* Why Choose Us */}
      <section style={{ backgroundColor: "#1c1a38", color: "white" }} className="py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <h2 className="text-4xl font-serif font-bold text-center mb-12">¬øPor qu√© Elegirnos?</h2>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            <div className="text-center">
              <div className="text-5xl mb-4">üå∏</div>
              <h3 className="font-serif text-2xl font-bold mb-2" style={{ color: "#b6cb51" }}>
                Flores Frescas
              </h3>
              <p className="text-gray-300">Flores selectas tra√≠das directamente de los mejores huertos</p>
            </div>

            <div className="text-center">
              <div className="text-5xl mb-4">‚ú®</div>
              <h3 className="font-serif text-2xl font-bold mb-2" style={{ color: "#b6cb51" }}>
                Dise√±o Personalizado
              </h3>
              <p className="text-gray-300">Crea tu arreglo perfecto seg√∫n tus necesidades y gustos</p>
            </div>

            <div className="text-center">
              <div className="text-5xl mb-4">üöö</div>
              <h3 className="font-serif text-2xl font-bold mb-2" style={{ color: "#b6cb51" }}>
                Entrega R√°pida
              </h3>
              <p className="text-gray-300">Enviamos tus flores el mismo d√≠a en √°rea metropolitana</p>
            </div>
          </div>
        </div>
      </section>

      <Footer />
    </div>
  )
}
